import loginCommon from './common/loginCommon.vue'
export default {
	components: {
		loginCommon
	},
	data() {
		return {
			// 控制发送以及剩余时间的切换显示
			isLogin: true,
			// 定时器命名
			timer: "",
			// 倒计时内容
			num: 6
		}
	},
	methods: {
		// 点击获取验证码
		getCode() {
			this.isLogin = false;
			// 调用定时器
			this.countDown();
		},
		// 倒计时效果
		countDown() {
			// 获取倒计时的初始值
			var time = this.num;
			// 为定时器命名
			this.timer = setInterval(() => {
				// 每隔一秒 num 就减一 实现同步
				time--;
				// 然后把 num 存进 data, 让用户知道时间在倒记着
				this.num = time;
				if (time == 0) {
					clearInterval(this.timer);
					// 当时间为 0 的时候 隐藏定时器的内容 显示发送的内容 并且为定时器重新赋值
					this.isLogin = true;
					this.num = 60;
				}
			}, 1000);
		},
		// 去登录页面
		goLogin() {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		}
	}
}
